import Bmob from "hydrogen-js-sdk";
import producers from '@/utils/producers.js';
import loading from "@/components/Loading/index.vue"

let that;
export default {
	components: {
		loading
	},
	data() {
		return {
			debt_list:[],
			name:'',
			loading: true,
		}
	},
	methods: {
		
	},
	onLoad(options) {
		//console.log(options)
		that = this;
		that.name = options.name
		producers.get_moneyList(options.id).then(res=>{
			that.loading = false
			that.debt_list = res
		});
	},
}
